import React, { ReactElement, useState, useEffect, CSSProperties, useCallback, useReducer, useMemo } from 'react';
import { InfoBar, Pairings, Sidebar, Profile, CrossTable, PasteInput, Switch, Dock } from './tournament/';
import { usePassable, useCacheColumns, useMouseEvents, useModes, usePlayerDict, useLoad } from './tournament/hooks/';
import { Settings, Automation, Public } from './tournament/Rightbar';
import { Player, Modes } from './interfaces';

import styles from './css/tournament.module.css';
import TabSection from './components/TabSection';
import { DisplayNameContext, LoadedContext } from './Contexts';
import { FaUserCircle, FaGlobeEurope, FaCog, FaRobot } from 'react-icons/fa';
import Banner from './tournament/Rightbar/Banner';
import FullScreen from './tournament/Rightbar/FullScreen';
import Announcements from './tournament/Announcement';

export const defaultColumns = [30.8, 38.4, 30.8];

interface TournamentProps {
	id: string
	closeTournament: () => void
	triggerError: (message: string) => void
	triggerConfirm: (message: string, resolve: () => void, reject: () => void) => void
	endSession: () => void
	setTournamentID: (id: string) => void
	setMode: (mode: Modes) => void
}

export default function Tournament(props: TournamentProps): ReactElement {

	const shouldRun = useMemo(() => !!props.id, [props.id]);

	const {
		columns, setColumns, resize, setResize, boundaries, loadColumns, cacheColumns		
	} = useCacheColumns(shouldRun);
	useEffect(() => {
		loadColumns();
	}, [loadColumns]);
	useEffect(() => {
		cacheColumns();
	}, [cacheColumns]);

	const {
		crossTable, pasteInput, setPasteInput, showAnnouncements, toggleAnnouncements, fullScreenSettings, toggleFullScreenSettings,
		switchMode, switchPairings, stateKey, setStateKey, rightTab, setRightTab, showRightTab, toggleRightTab, toggleCrossTable,
		sidebar, toggleSidebar, displayPlayer, setDisplayPlayer
	} = useModes({
		columns, setColumns, boundaries
	});

	useMouseEvents({
		columns, boundaries, resize, setColumns, setResize, toggleSidebar, toggleRightTab, shouldRun
	});

	const {
		players, playerDictKey, setDictTimeout, playerDict, setPlayerDict, updatePlayerDict
	} = usePlayerDict({
		id: props.id, displayPlayer, setDisplayPlayer
	});

	const {
		status, settings, announcements, rounds, updateTournamentStatus, updateTournamentSettings, updateSetting, updateRounds, load
	} = useLoad({
		id: props.id, updatePlayerDict
	});

	const {
		nextRound, checkIfEnoughPlayers, lastRoundOverride, setLastRound, getDisplayName, search, currSearch,
		filteredPairings, filterPairings, userRound, setRound, pairingFilters, setPairingFilters,
		outstandingIndex, nextOutstanding, canSetOutstanding, setOutstanding
	} = usePassable({
		players, settings, playerDict, playerDictKey
	});
    
	useEffect(() => {
		if (!setDictTimeout) return;
		let x = setTimeout(updatePlayerDict, 5000);
		return () => clearTimeout(x);
	}, [setDictTimeout, updatePlayerDict]);
	useEffect(() => {
		load();
	}, [load]);

	const style: CSSProperties = useMemo(() => {
		let s = {
			cursor: resize ? 'ew-resize' : undefined,
			gridTemplateColumns: '70px ' + columns.join('fr ') + 'fr'
		};
		return s;
	}, [resize, columns]);

	if (fullScreenSettings) return <div className={styles.tournament}>
		<FullScreen
			updateTournamentSettings={updateTournamentSettings}
			updateSetting={updateSetting}
			status={status}
			settings={settings}
			setLastRound={setLastRound}
			triggerError={props.triggerError}
			players={players}
			updateRounds={updateRounds}
			rounds={rounds}
			updateState={() => setStateKey(Math.random().toString(16).slice(2, 9))}	
			leave={() => toggleFullScreenSettings(false)}
		/>
	</div>;

	if (crossTable) return <div className={styles.tournament}>
		<CrossTable
			show={toggleCrossTable}
			setDisplayPlayer={setDisplayPlayer}
			status={status}
			players={players}
			settings={settings}
			rightTab={showRightTab}
			toggleRightTab={() => toggleRightTab({set: !showRightTab })}
		/>
		{showRightTab ? 
			<div className={styles.profile}>
				<TabSection
					name={rightTab}
					setName={(name: string) => setRightTab(name as any as 'settings' | 'player' | 'public')}
					nameDict={{
						player: <><FaUserCircle />Profile</>,
					}}
					hasLoaded={!!settings.pairingSystem}
				/>
				<DisplayNameContext.Provider value={getDisplayName}>
					<Profile
						id={props.id}
						status={status}
						playerID={displayPlayer}
						setDisplayPlayer={setDisplayPlayer}
						playerDict={playerDict}
						modifyPlayerDict={(player: Player, hash: string) => setPlayerDict({ player, hash })}
						settings={settings}
					/>
				</DisplayNameContext.Provider>
			</div> :
			null}
	</div>;

	if (pasteInput) return <div className={styles.background}>
		<div className={styles.tournament}>
			<PasteInput
				status={status}
				settings={settings}
				id={props.id}
				updatePlayerDict={updatePlayerDict}
				players={players}
				leave={() => setPasteInput(false)}
			/>
		</div>		
	</div>;
	else if (showAnnouncements !== -1) return <div className={styles.background}>
		<div className={styles.tournament}>
			<Announcements
				id={props.id}
				initialA={showAnnouncements}
				prev={announcements}
				leave={() => toggleAnnouncements(-1)}
			/>
		</div>		
	</div>;

	return (
		<LoadedContext.Provider value={!!settings.pairingSystem}>
			<DisplayNameContext.Provider value={getDisplayName}>
				<div className={styles.tournament} style={style}>
					{!sidebar ? null : <Sidebar
						playerDict={playerDict}
						playerDictKey={playerDictKey}
						updatePlayerDict={updatePlayerDict}
						setPlayerDict={setPlayerDict}
						
						settings={settings}
						id={props.id}
						setDisplayPlayer={setDisplayPlayer}
						checkIfEnoughPlayers={checkIfEnoughPlayers}
						togglePasteInput={() => setPasteInput(true)}
						stateKey={stateKey}
						updateSetting={updateSetting}
						jumpedTo={currSearch}
					/>}
					<Dock
						togglePasteInput={() => setPasteInput(true)}
						toggleFullScreen={() => toggleFullScreenSettings(true)}
						toggleAnnouncements={toggleAnnouncements}
						userRound={userRound}
						load={load}
						status={status}
						settings={settings}
						closeTournament={props.closeTournament}
						toggleCrossTable={toggleCrossTable}
						setLastRound={setLastRound}
						lastRoundOverride={lastRoundOverride}
						triggerError={props.triggerError}
						switchMode={switchMode}
						switchPairings={switchPairings}
						canReset={!columns.every((c, i) => defaultColumns[i] === c)}
						resetColumns={() => {
							setColumns(defaultColumns);
							toggleRightTab({ set: true });
							toggleSidebar({ set: true });
						}}
						pairingFilters={pairingFilters}
						setFilters={setPairingFilters}
						nextOutstanding={nextOutstanding}
						canSetOutstanding={canSetOutstanding}
					/>
					<InfoBar
						userRound={userRound}
						setRound={setRound}
						filterPairings={filterPairings}
						status={status}
						settings={settings}
						updateTournamentStatus={updateTournamentStatus}
						totalRounds={settings.totalRounds}
						lastRoundOverride={lastRoundOverride}
						players={players}
						switchPairings={switchPairings}
						search={(id: string) => {
							search(id);
							setDisplayPlayer(id);
						}}
					/>
					{switchMode ? <Switch
						status={status}
						playerDict={playerDict}
						updatePlayerDict={updatePlayerDict}				
						settings={settings}
						playerDictKey={playerDictKey}
						setDisplayPlayer={setDisplayPlayer}
						filtered={filteredPairings}
					/> : <Pairings
						userRound={userRound}
						status={status}
						playerDict={playerDict}
						updatePlayerDict={updatePlayerDict}
						updateStatus={updateTournamentStatus}
						settings={settings}

						id={props.id}
						canTriggerNextRound={nextRound}
						toggleSidebar={() => toggleSidebar({})}
						toggleRightTab={() => toggleRightTab({set: !showRightTab})}
						setDisplayPlayer={setDisplayPlayer}
						updateRound={updateTournamentStatus}
						updateTournamentSettings={updateTournamentSettings}
						updateSetting={updateSetting}
						setLastRound={setLastRound}
						lastRoundOverride={lastRoundOverride}
						toggleCrossTable={toggleCrossTable}
						playerDictKey={playerDictKey}
						triggerConfirm={props.triggerConfirm}
						filtered={filteredPairings}
						filter={filterPairings}
						filters={pairingFilters}
						jumpedTo={currSearch}
						outstandingIndex={outstandingIndex}
						setOutstanding={setOutstanding}
					/>}
					{showRightTab ? <>
						<div className={styles.profile}>
							<div className={styles.profileStage}>
								<TabSection
									name={rightTab}
									setName={(name: string) => setRightTab(name as any as 'settings' | 'player' | 'public')}
									nameDict={{
										settings: <><FaCog style={{color: 'lightgrey'}} /><div>Settings</div></>,
										player: <><FaUserCircle style={{color: '#385898'}} /><div>Profile</div></>,
										automation: <><FaRobot style={{color: 'gold'}} /><div>Automation</div></>,
										public: settings.public ? <><FaGlobeEurope style={{color: 'green'}} /><div>Public</div></> : ''
									}}
									hasLoaded={!!settings.pairingSystem}
								/>
								{
									rightTab === 'settings' ?
										<Settings
											updateTournamentSettings={updateTournamentSettings}
											updateSetting={updateSetting}
											status={status}
											settings={settings}
											setLastRound={setLastRound}
											triggerError={props.triggerError}
											players={players}
											updateRounds={updateRounds}
											rounds={rounds}
											updateState={() => setStateKey(Math.random().toString(16).slice(2, 9))}	
										/> :
										rightTab === 'player' ?
											<Profile
												id={props.id}
												status={status}
												playerID={displayPlayer}
												setDisplayPlayer={setDisplayPlayer}
												playerDict={playerDict}
												modifyPlayerDict={(player: Player, hash: string) => setPlayerDict({ player, hash })}
												settings={settings}
												playerDictKey={playerDictKey}
											/>
											:
											rightTab === 'public' ?
												<Public
													updateTournamentSettings={updateTournamentSettings}
													updateSetting={updateSetting}
													status={status}
													settings={settings}
													setLastRound={setLastRound}
													triggerError={props.triggerError}
													players={players}
													updateRounds={updateRounds}
													rounds={rounds}
												/> :
												rightTab === 'automation' ?
													<Automation
														updateTournamentSettings={updateTournamentSettings}
														updateSetting={updateSetting}
														status={status}
														settings={settings}
														setLastRound={setLastRound}
														triggerError={props.triggerError}
														players={players}
														updateRounds={updateRounds}
														rounds={rounds}
														updateState={() => setStateKey(Math.random().toString(16).slice(2, 9))}	
													/> :
													null
								}
							</div>
						</div>
						<Banner
							id={props.id}
							toggleFullScreen={() => toggleFullScreenSettings(true)}
							displayPlayer={rightTab === 'player' ? displayPlayer : ''}
							clearDisplayPlayer={() => setDisplayPlayer('')}
							modifyPlayerDict={(player: Player, hash: string) => setPlayerDict({ player, hash })}
							toggleAnnouncements={toggleAnnouncements}
							rightTab={rightTab}
						/>
					</>: null}
				</div>
				{/*</div>*/}
			</DisplayNameContext.Provider>
		</LoadedContext.Provider>
	);

}

