import React from 'react';
import { win } from './utils/electron';
import { FaWindowMaximize, FaWindowRestore, FaWindowMinimize, FaWindowClose } from 'react-icons/fa';

import styles from './css/statusbar.module.css';

const Package = require('../package.json');

function toggleMaximisation () {
	if (win.isMaximized()) {
		win.restore();
	} else {
		win.maximize();
	}
}

export default function StatusBar() {

	const initialName = ['ScorchApp', 'v.' + Package.version].join(' ');

	return (
		<div className={styles.statusBar}>
			<div className={styles.title}>
				{initialName}
			</div>
			<div className={styles.menu}>
				<div className={styles.favicon}>
					<img
						alt='favicon'
						draggable={false}
						src={process.env.PUBLIC_URL + '/logo192.png'}
					/>
				</div>
			</div>
			<div className={styles.exec}>
				<div onClick={() => win.minimize()}>
					<FaWindowMinimize />
				</div> 
				<div onClick={toggleMaximisation} >
					{win.isMaximized() ? 
						<FaWindowRestore /> :
						<FaWindowMaximize />
					}
				</div> 
				<div onClick={() => win.close()} className={styles.close}>
					<FaWindowClose />
				</div>
			</div>
		</div>
	);
}