import React, { ReactElement, useState, useReducer, useCallback, useEffect, useMemo } from 'react';
import './utils/wdyr';
import './App.css';
import Login from './tournament/Login';
import StatusBar from './StatusBar';
import Tournament from './Tournament';
import { POST, cookies, setErrorCallback, GET } from './utils/requests';
//import { isElectron, isDev, isProd } from './utils/electron';

import Menu from './tournament/Menu';
import Alert, { AlertProps } from './tournament/Alert';
import { useStateWithLabel } from './utils/prototype';
import { AlertContext } from './Contexts';
import { User, Modes } from './interfaces';
import { FaExclamationTriangle } from 'react-icons/fa';
import { Intro } from './tournament/';

const startMode = 'menu' as Modes;

export default function App(): ReactElement {

	const [sessionID, setSession] = useState('');
	const [tournamentID, setTournament] = useState('');
	const [mode, setMode] = useStateWithLabel('' as Modes, 'mode');
	useEffect(() => {
		(window as any).setMode = setMode;
	}, [setMode]);
	const [alertBox, setAlertBox] = useReducer((state: AlertProps, action: AlertProps) => {
		return Object.assign({}, state, action);
	}, { message: '', title: '', type: '' });

	const closeAlertBox = useCallback((): void => setAlertBox({ message: '', title: '' }), [setAlertBox]);
	const triggerConfirm = useCallback((message: string, resolve: () => void, reject: () => void) => setAlertBox({
		message,
		title: 'CONFIRM ACTION',
		type: 'confirm',
		resolve,
		reject
	}), [setAlertBox]);
	const triggerError = useCallback((message: string) => setAlertBox({message, title: 'ERROR', type: 'error' }), [setAlertBox]);

	const setTournamentID = useCallback((tournamentID: string): void => {
		setTournament(tournamentID);
		setMode(tournamentID ? 'tournament' : 'menu');
	}, [setTournament, setMode]);

	const hasLoggedIn = useCallback(async (sessionID: string): Promise<void> => {
		await cookies.set('sessionID', sessionID, { path: '/' });
		setSession(sessionID);
		setMode(startMode);
	}, [setSession, setMode]);

	const closeTournament = useCallback(async (): Promise<void> => {
		setTournament('');
		await cookies.set('tournamentID', '', { path: '/' });
		setMode('menu');
	}, [setTournament, setMode]);

	const endSession = useCallback(async (): Promise<void> => {
		try {
			await POST({ url: '/endSession' });
		} catch (e) {
			console.error(e);
		} finally {
			await cookies.set('sessionID', '', { path: '/' });
			document.cookie = 'sessionID=;';
			setSession('');
			setMode('login');
		}
	}, [setSession, setMode]);
	
	useEffect(() => {
		setErrorCallback((header: string, e: string) => setAlertBox({
			title: <><FaExclamationTriangle />{header}</>,
			type: 'error',
			message: typeof e === 'string' ? e.startsWith('<!DOCTYPE html>') ? <div dangerouslySetInnerHTML={{ __html: e }} /> : <>{e.split('\n').map((line, i) => <p key={['e', i].join('.')}>{line}</p>)}</> : JSON.stringify(e, null, 4)
		}));
		return () => setErrorCallback(() => {});
	}, [triggerError]);

	const pageLoad = useCallback(async () => {
		let sessionID = await cookies.get('sessionID');
		if (!sessionID) return;
		setSession(sessionID);
		setMode(startMode);
	}, [setSession, setMode]);

	useEffect(() => {
		pageLoad();
	}, [pageLoad]);

	const [user, setUser] = useStateWithLabel({} as User, 'user');
	const updateUser = useCallback((): Promise<void> => !sessionID ? null : GET({ url: '/profile'})
		.then(setUser), [setUser, sessionID]);

	useEffect(() => {
		updateUser();
	}, [updateUser]);

	const Mode: ReactElement = useMemo(() => {
		switch (mode) {
		case 'login': {
			return <Login login={hasLoggedIn} mode='login' />;
		}
		case 'menu': {
			return <Menu
				endSession={endSession}
				setTournamentID={setTournamentID}
				triggerConfirm={triggerConfirm}
				user={user}
				updateUser={updateUser}
				setMode={setMode}
			/>;
		}
		case 'intro': {
			return <Intro
				endSession={endSession}
				setTournamentID={setTournamentID}
				setMode={setMode}
			/>;
		}
		case 'tournament': {
			return null;
			return <Tournament
				id={tournamentID}
				closeTournament={closeTournament}
				triggerError={triggerError}
				triggerConfirm={triggerConfirm}
				endSession={endSession}
				setTournamentID={setTournamentID}
				setMode={setMode}
			/>;
		}
		default: {
			setMode('login');
			return null;
		}
		}
	}, [user, updateUser, mode, hasLoggedIn, sessionID, endSession, setTournamentID, triggerConfirm, tournamentID, closeTournament, triggerError, setMode]);

	return (
		<>
			<StatusBar />
			<div className='stage'>
				<AlertContext.Provider value={{
					setAlert: setAlertBox,
					closeAlert: closeAlertBox,
					startLoading: () => setAlertBox({ message: true, type: 'loading' }),
					stopLoading: () => setAlertBox({ message: '', type: '' })
				}}>
					{mode === 'tournament' ? null : <div className={['shade', 'blurred'].join(' ')}>
						{Mode}
					</div>}
					<Tournament
						id={tournamentID}
						closeTournament={closeTournament}
						triggerError={triggerError}
						triggerConfirm={triggerConfirm}
						endSession={endSession}
						setTournamentID={setTournamentID}
						setMode={setMode}
					/>
				</AlertContext.Provider>
				{alertBox.message && (typeof alertBox.message === 'string' ? alertBox.message.length > 10 : true) ? <Alert
					{...alertBox}
					close={closeAlertBox}
				/> : null}
			</div>
		</>
	);

}