import { createContext } from 'react';
import { Player } from './interfaces';
import { AlertProps } from './tournament/Alert';

export const PlayerDictContext = createContext(new Map() as Map<string, Player>);
export const DisplayNameContext = createContext((() => '') as (id: string | Player) => string);
export const AlertContext = createContext({
	setAlert: (props: AlertProps) => {},
	closeAlert: () => {},
	startLoading: () => {},
	stopLoading: () => {}
});
export const LoadedContext = createContext(false);